@import "../base/_variables.scss";
@import "../base/_placeholders.scss";
@import "../base/_mixins.scss";

.contact-bar-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  .item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;

    .icon {
      border: 1px solid $white;
      opacity: 0.8;
      padding: 10px;
      margin: 8px 2px;
      transition: all 0.5s ease-in-out;
      width: 30px;
      height: 30px;
    }

    .icon-small {
      border: none;
      padding: 5px;
      margin: 3px;
      width: 20px;
      height: 20px;
    }

    .icon-even {
      animation: iconEvenStartAnimation 1s ease-in-out;
    }

    .icon-odd {
      animation: iconOddStartAnimation 1s ease-in-out;
    }

    .texto {
      opacity: 1;
      font-weight: 200;
      margin-left: 10px;
      transition: all 0.5s ease-in-out;
    }

    &:hover {
      .icon-even {
        transform: rotateX(360deg);
      }

      .icon-odd {
        transform: rotateY(360deg);
      }

      .texto {
        transform: translateX(10px);
      }
    }
  }
}

.contact-bar-container-horizontal {
  flex-direction: row;
}

@keyframes iconEvenStartAnimation {
  from {
    transform: rotateX(0);
    margin-left: 10px;
  }
  to {
    transform: rotateX(360deg);
    margin-left: 2px;
  }
}

@keyframes iconOddStartAnimation {
  from {
    transform: rotateY(0);
    margin-left: 10px;
  }
  to {
    transform: rotateY(360deg);
    margin-left: 2px;
  }
}

@media only screen and (max-width: $breakpoint-tablet) and (orientation: portrait) {
  .contact-bar-container .item .texto {
    font-size: 1.15rem;
  }
}
