@import "_variables.scss";

%spread-full-window {
    width: 100vw;
    height: 100vh;
}

%perspective {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    perspective-origin: 0 0;
}

%perspective-box {
    @extend %perspective;
    perspective: $perspective-max-depth; // eg. 2px
    -webkit-perspective: $perspective-max-depth;
}

%perspective-element-default {
    // default shallow element
    transform-origin: 0 0;
    transform: translateZ($perspective-shallow-depth) scale(perspective-element-scale($perspective-shallow-depth));
}

%perspective-element-deep {
    @extend %perspective-element-default;
    transform: translateZ($perspective-deep-depth) scale(perspective-element-scale($perspective-deep-depth));
}

%perspective-element-middle {
    @extend %perspective-element-default;
    transform: translateZ($perspective-middle-depth) scale(perspective-element-scale($perspective-middle-depth));
}

%img-full-cover {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: bottom;
}

%section {
    padding: 5vh 0;
}

%grid {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    // overflow-y: scroll;
    scroll-behavior: smooth;
    margin: 8px 0;
}
