$white: #fff;
$black: #000;
$dark-grey: #2b2b2b;
$dark-green: #072b2a;
$lighter-green: #29524A;
$dark-blue: #0c1821;

$gradient-black-blue: linear-gradient(
  to bottom,
  $black,
  #0b060a,
  #0e0d15,
  #0c141c,
  #041a21,
  #042025,
  #042528,
  $dark-green
);

$default-font-color: $white;
$default-background: $black;

$perspective-max-depth: 5px;
$perspective-shallow-depth: 0px;
$perspective-middle-depth: -$perspective-max-depth / 2;
$perspective-deep-depth: -$perspective-max-depth;

@function perspective-element-scale($depth-position) {
  //  (perspective-translation)/perspective.
  @return ($perspective-max-depth - $depth-position) / $perspective-max-depth;
}

@function perspective-element-px-scale($px, $depth-position) {
  //  (perspective-translation)/perspective.
  @return $px * ($perspective-max-depth - $depth-position) / $perspective-max-depth;
}


/* Using SCSS variables to store breakpoints */
$breakpoint-tablet: 768px;

$padding-lateral:20px;