@import "../base/_variables.scss";
@import "../base/_placeholders.scss";
@import "../base/_mixins.scss";

.grid-container {
    @extend %section;
    background-color: $dark-blue;
}

.grid {
    @extend %grid;
}

.grid-element-container {
    width: 390px;
    height: 600px;
    position: relative;
}

.grid-element-img-container {
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 0;
    overflow: hidden;

    img {
        @extend %img-full-cover;
    }
}

.grid-element-text-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 8px;
    box-sizing: border-box;
    background-color: rgba(255, 255, 255, 0.9);
    opacity: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    color: $black !important;
    transition: all 0.3s ease-in-out;

    h6 {
        font-weight: 400;
    }

    &:hover {
        opacity: 1;
    }

    button {
        background-color: transparent;
        text-decoration: none;
        border: 2px solid $lighter-green;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
        padding: 5px 15px;

        &:hover {
            background-color: $lighter-green;
            color: $white;
        }

        p {
            font-weight: 400;
        }
    }
}

@media only screen and (max-width: $breakpoint-tablet) and (orientation: portrait) {
    .grid-element-container {
        width: 95%;
    }
}

.grid-element-modal-content-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
}

.grid-element-modal-img-container {
    width: 100%;
    height: 50%;
    position: relative;
    display: flex;
    left: 0;
    transition: all 0.5s ease-in-out;

    div {
        height: 100%;
        width: 100vw;
    }

    img {
        width: auto;
        height: 100%;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }  
}

.grid-element-modal-img-selector-container{
    width:100%;
    padding:10px;
    display:flex;
    justify-content: center;

    button{
        cursor:pointer;
        background-color: $dark-grey;
        text-decoration: none;
        border:none;
        box-shadow: 0 0 5px 0 rgba(0,0,0,0.3);
        margin:5px;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        opacity:0.5;
        transition: all 0.3s ease-in-out;

        &:hover{
            opacity:1;
        }

        &.modal-img-selector-button-selected{
            background-color: $lighter-green;
            opacity:1;
        }
    }
}

.grid-element-modal-text-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 5px 15px;
    flex-grow: 1;

    p {
        font-weight: 400;
    }
}

.grid-element-modal-links-container {
    width: 100%;
    background-color: $dark-green;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    .link {
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.8;
        padding: 5px 10px;
        margin: 8px;
        // border: 1px solid $white;
        // border-radius: 5%;
        transition: all 0.5s ease-in-out;
        cursor: pointer;

        a {
            display: flex;
            justify-content: center;
            align-items: center;
            text-decoration: none;
            color: $white;
        }

        svg,
        img {
            width: 25px;
            height: 25px;
        }

        &:hover {
            svg {
                transform: rotateY(360deg);
            }
        }

        p {
            margin-left: 8px;
        }
    }
