@import "../base/_variables.scss";
@import "../base/_placeholders.scss";
@import "../base/_mixins.scss";

// _HOME

.home {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: relative;
  background: $black;
  // background: $gradient-black-blue;
}

.home-parallax {
  @extend %perspective-box;

  .home-background-img {
    @extend %perspective-element-deep;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    display: flex;
    justify-content: center;

    img {
      @extend %img-full-cover;
    }
  }

  .home-background-overlay {
    @extend %perspective-element-deep;
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $black;
    opacity: 0.3;
  }

  .home-title-container {
    @extend %perspective-element-default;
    position: absolute;
    top: calc(50% - 5rem);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    padding-left: $padding-lateral;

    .home-title {
      color: $white;
      width: 100%;
      margin: 10px 0;
      text-align: center;
    }

    .home-subtitle {
      display: inline-block;
      margin: 0 15px;
      font-weight: 200;
    }

    .specialities {
      margin: auto;
    }
  }
}

.home-arrow-container{
  @extend %perspective-element-default;
  position: absolute;
  bottom: 15%;
  width:100%;
  display: flex;
  justify-content: center;

  .arrow{
    width:50px;
    height:50px;
    border-radius: 50%;
    border: 1px solid $white;
    display:flex;
    justify-content: center;
    align-items: center;
    cursor:pointer;
  }
}

.home-profile-avatar {
  @extend %perspective-element-middle;
  width: 50vw;
  height: 80vh;
  position: relative;
  top: perspective-element-px-scale(100vh, $perspective-middle-depth);
  left: perspective-element-px-scale(40vw, $perspective-middle-depth);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  z-index: 2;

  img {
    @extend %img-full-cover;
  }
}

.home-profile-about-me {
  @extend %perspective-element-default;
  width: 75%;
  position: relative;
  top: perspective-element-px-scale(110vh, $perspective-shallow-depth);
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.7);
  padding: 8px;
  box-sizing: border-box;
  scroll-margin:  perspective-element-px-scale(30vh, $perspective-middle-depth);
}

.home-profile-contact {
  @extend %perspective-element-default;
  position: relative;
  top: perspective-element-px-scale(170vh, $perspective-shallow-depth);
  left: $padding-lateral;
  display: flex;
  flex-direction: column;
  z-index: 2;
}

.home-content {
  @extend %perspective-element-default;
  position: relative;
  top: perspective-element-px-scale(200vh, $perspective-shallow-depth);
  z-index: 3;
}

.animated-separator {
  background-color: $white;
  height: 2px;
  width: 100%;
  margin: 10px 0;
  transition: all 1s ease;
  animation: separatorAnimation 1s ease-in-out;
}

@keyframes separatorAnimation {
  0% {
  }
  50% {
    margin-left: 50%;
    width: 0%;
    opacity: 0.3;
  }
  100% {
    margin-left: 0%;
    width: 100%;
    opacity: 1;
  }
}

@media only screen and (max-width: $breakpoint-tablet) and (orientation: portrait) {
  .home-title-container {
    width: 90%;
    align-items: center !important;

    .specialities {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  .home-subtitle {
    display: flex !important;
  }
  .home-parallax {
    .home-title {
      bottom: inherit;
      top: 15%;
    }

    .home-background-img {
      img {
        object-position: 65% !important;
      }
    }
  }

  .home-profile-avatar {
    width: 90vw;
    left: perspective-element-px-scale(5vw, $perspective-middle-depth);
  }

  .home-profile-contact {
    top: perspective-element-px-scale(150vh, $perspective-shallow-depth);
  }
}
