@import "../base/_variables.scss";
@import "../base/_placeholders.scss";
@import "../base/_mixins.scss";

.topbar {
  position:fixed;
  top:0;
  left:0;
  background-color: $black;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width:100%;
  box-sizing: border-box;
  z-index: 1000;

  .topbar-lang{
    display:flex;
    align-items: center;

    p{
      cursor:pointer;
      margin: 0 5px;
    }
  }
  
  .lang-active{
    padding-top:3px;
    border-bottom: 3px solid $lighter-green;
    font-weight: 400;
  }
}